* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.box {
  justify-content: center;
  align-items: center;
  gap: 40px;
  margin-top: 200px;
  display: flex;
}

.switcher-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  outline: none;
  padding: 20px 40px;
  font-size: 20px;
  font-weight: bold;
  display: inline-block;
}

.switcher-btn:hover, .switcher-btn:focus {
  background-color: #3e8e41;
}

.switcher-btn:disabled {
  opacity: .5;
  cursor: not-allowed;
}

.switcher-btn:active {
  background-color: #357835;
}

